@import url('https://fonts.googleapis.com/css?family=Fjalla+One&display=swap');html,body, #root{
    position: relative;
    width:100%;
    height:100%;
    margin:0;
}


#root{
    background:url('./wallpaper.jpg') no-repeat;
    background-size:cover;
}

#root > .container{
    width:40%;
    padding:1vw 5vw;
    font-family: 'Fjalla One', sans-serif;
    z-index:1;
}

h1{
    font-size:100px;
    color:#fff;
    margin-bottom:20px;
}
p{
    line-height:30px;
    color:rgba(255,255,255,0.7);
    margin-bottom:40px;
}
input{
    width:70%;
    height:200px;
    background:none;
    border:none;
    outline: none;
    background:rgba(255,255,255,0.4);
    padding:20px;
    text-align: center;
    box-sizing: border-box;
    color:#999;
    font-size:22px;
    letter-spacing: 1px;

}

.bg{
    position: absolute;
    top:0;
    right:-50vw;
    width:100vw;
    height:100vh;
    background:linear-gradient(to right , rgba(0,0,0,0), black);
}


button{
    display:inline-block;
    margin-top:60px;

    background:none;
    color:#fff;
    width:220px;
    height:50px;
    border:none;
    font-size:16px;
    font-family: 'Fjalla One', sans-serif;
    margin-right:25px;
    border:2px solid #fff;
}